<template>
    <div class="main-div app">
        <div class="row text-center">
            <div class="col-12">
                <p class="heading">READ MY FACE</p>
                <div class="row">
                    <div class="col-12">
                        <!-- <p style="color:#0892D0"><strong>Neutral: </strong>{{neutral}}%</p>
                        <p style="color:#0892D0"><strong>Happy: </strong>{{happy}}%</p>
                        <p style="color:#0892D0"><strong>Sad: </strong>{{sad}}%</p>
                        <p style="color:#0892D0"><strong>Angry: </strong>{{angry}}%</p> -->
                        <div class="row mb-3">
                            <div class="col-2 tag">Neutral</div>
                                <div class="col-10 mt-1">
                                <div class="progress ml-5 mr-5 text-right">
                                    <div id="neutral-div" class="progress-bar" role="progressbar" >{{neutral}}</div>
                                </div>
                            </div>
                        </div>
                        <div class="row mb-3 ">
                            <div class="col-2 tag">Happy</div>
                                <div class="col-10 mt-1">
                                <div class="progress ml-5 mr-5 text-right">
                                    <div id="happy-div" class="progress-bar" role="progressbar" >{{happy}}</div>
                                </div>
                            </div>
                        </div>
                        <div class="row mb-3">
                            <div class="col-2 tag">Sad</div>
                                <div class="col-10 mt-1">
                                <div class="progress ml-5 mr-5 text-right">
                                    <div id="sad-div" class="progress-bar" role="progressbar" >{{sad}}</div>
                                </div>
                            </div>
                        </div>
                        <div class="row mb-3">
                            <div class="col-2 tag">Disgusted</div>
                                <div class="col-10 mt-1">
                                <div class="progress ml-5 mr-5 text-right">
                                    <div id="disgusted-div" class="progress-bar" role="progressbar">{{disgusted}}</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="row text-center mt-2">
            <div class="col-12">
                <button class="btn blue mr-2" @click="playVideo()">Start</button>
                <button class="btn blue mr-2" @click="pauseVideo()">Stop</button>
                <button class="btn blue" @click="startDetection()">Analyze</button>
                <!-- <button class="main-btn btn btn-warning" @click="generateGraphs()">Generate Graph</button> -->
            </div>
        </div>
        <div class="row">
            <div class="col-12">
                <video capture="camera" class="video-element" id="video" width="300" height="400" playsinline muted></video>
            </div>
        </div>
        <div class="col-12">
            <div id="canvas-div" class="row canvas-div"></div>
        </div>
    </div>
</template>

<script>
import * as faceapi from 'face-api.js'
import ProgressBar from 'progressbar.js'

export default {
    name:'FaceDetection',
    mounted() {
        this.startVideo();
    },
    data() {
        return {
            neutral:0,
            happy:0,
            sad:0,
            disgusted:0
        }
    },
    methods:{
        startVideo() {
            navigator.getUserMedia(
            { video: {} },
            stream => video.srcObject = stream,
            err => console.error(err)
            )
        },
        makeGraph() {
            var neutralDiv = document.getElementById('neutral-div').setAttribute('style','width:'+this.neutral+'%');
            var happyDiv = document.getElementById('happy-div').setAttribute('style','width:'+this.happy+'%');
            var sadDiv = document.getElementById('sad-div').setAttribute('style','width:'+this.sad+'%');
            var angryDiv = document.getElementById('disgusted-div').setAttribute('style','width:'+this.disgusted+'%');
        },
        pauseVideo() {
            
            var video_element = document.getElementById('video');
            video_element.pause();
        },
        playVideo() {
            var video_element = document.getElementById('video');
            video_element.play();
        },
        startDetection() {
            // Promise.all([
            //     faceapi.nets.tinyFaceDetector.loadFromUri('http://192.168.0.104:8081/models/'),
            //     faceapi.nets.faceLandmark68Net.loadFromUri('http://192.168.0.104:8081/models/'),
            //     faceapi.nets.faceRecognitionNet.loadFromUri('http://192.168.0.104:8081/models/'),
            //     faceapi.nets.faceExpressionNet.loadFromUri('http://192.168.0.104:8081/models/')
            // ]).then(this.detectExpression())
            Promise.all([
                faceapi.nets.tinyFaceDetector.loadFromUri('https://facialrecogntn.firebaseapp.com/models'),
                faceapi.nets.faceLandmark68Net.loadFromUri('https://facialrecogntn.firebaseapp.com/models'),
                faceapi.nets.faceRecognitionNet.loadFromUri('https://facialrecogntn.firebaseapp.com/models'),
                faceapi.nets.faceExpressionNet.loadFromUri('https://facialrecogntn.firebaseapp.com/models')
            ], {mode:'no-cors'}).then(this.detectExpression())
        },
        detectExpression() {
            const video = document.getElementById('video');
            const canvas = faceapi.createCanvasFromMedia(video);
            const displaySize = 
            {
                width: video.width,
                height: video.height 
            }
            faceapi.matchDimensions(canvas, displaySize);
            setInterval(async () => {
                const detections = await faceapi.detectAllFaces(video, new faceapi.TinyFaceDetectorOptions()).withFaceLandmarks().withFaceExpressions()
                if(detections.length>0) {
                    console.log(detections);
                    // console.log(detections[0].expressions);
                    this.neutral = ((detections[0].expressions.neutral)*100).toFixed(2);
                    this.happy =((detections[0].expressions.happy)*100).toFixed(2);
                    this.sad = ((detections[0].expressions.sad)*100).toFixed(2);
                    this.disgusted= ((detections[0].expressions.disgusted)*100).toFixed(2);
                    this.makeGraph();
                }
            },1000)
        }
    }
}
</script>

<style lang="css" scoped>
@import url('http://fonts.googleapis.com/css?family=Kavoon');
.main-btn{
    margin: 0 auto;
}
.tag {
    color:white;
    font-family: 'Courier New', Courier, monospace;
    font-weight: 600
}
.video-element {
   /* border: 2px black dashed; */
    margin: 0 auto;
    transform: rotateY(180deg);
    -webkit-transform:rotateY(180deg); /* Safari and Chrome */
    -moz-transform:rotateY(180deg); /* Firefox */
}
.main-div {
    margin:0 auto;
    text-align: center;
}
.progress-bar {
    background-color: #0892D0;
}
.heading {
    font-size:36px;
    margin-top: -50px;
    font-weight: 600;
    color: white;
    font-family: 'Sofia';
}
#buttons {
  padding-top: 0px;
  text-align: center;
}

/* start da css for da buttons */
.btn {
  border-radius: 5px;
  padding-bottom: 15px;
  height:35px;
  width:100px;
  font-size: 15px;
  font-family: 'Courier New', Courier, monospace;
  text-decoration: none;
  margin: 10px;
  color: black;
  font-weight: bold;
  position: relative;
  display: inline-block;
}

.btn:active {
  transform: translate(0px, 5px);
  -webkit-transform: translate(0px, 5px);
  box-shadow: 0px 1px 0px 0px;
}

.blue {
  background-color: #55acee;
  box-shadow: 0px 5px 0px 0px #3C93D5;
}

.blue:hover {
  background-color: #6FC6FF;
}

.green {
  background-color: #2ecc71;
  box-shadow: 0px 5px 0px 0px #15B358;
}

.green:hover {
  background-color: #48E68B;
}

.red {
  background-color: #e74c3c;
  box-shadow: 0px 5px 0px 0px #CE3323;
}

.red:hover {
  background-color: #FF6656;
}

@media only screen and (max-width: 600px) {
    .heading {
        font-size:25px;
        
    }
    .video-element {
        margin-top: 15px;
    }
}
</style>


